/* Gallery */
.gallery .img {
    width: 315px;
    height: 200px;
}
.gallery img {
    width: 315px;
    height: 200px;
    object-fit: cover;
}

/* Gallery */

@media screen and (max-width: 768px) {
    .gallery .img,
    .gallery img {
        width: 100%;
        height: 400px;
    }
}





